import React from 'react';
import { connect } from 'react-redux';
import { Card, Table, Tag, Tooltip, message, Button, Col, Row, Popconfirm, Radio } from 'antd';
import { DownloadOutlined, EyeOutlined, UploadOutlined } from '@ant-design/icons';
import axios from 'axios';

const DesignerOrders = (props) => {
    let { user_id, role_id, history } = props;

    let [loadingForwarded, setLoadingForwarded] = React.useState(true);
    let [loadingPicked, setLoadingPicked] = React.useState(true);
    let [forwardedDeals, setForwardedDeals] = React.useState([]);
    let [pickedDeals, setPickedDeals] = React.useState([]);

    const columns = [
        {
            title: 'Deadline',
            dataIndex: 'order_deadlinedate',
            key: 'order_deadlinedate',
            align: 'center',
            render: text => <span>
                <Tag >
                    {text}
                </Tag>
            </span>,
        },
        {
            title: 'Design Name',
            dataIndex: 'order_designname',
            key: 'order_designname',
            align: 'center',
        },
        {
            title: 'Assign To',
            dataIndex: 'assign_user',
            key: 'assign_user',
            align: 'center',
            render: text => {
                return (<span>{text || 'Inhouse'}</span>)
            }
            ,
        },
        {
            title: 'Status',
            dataIndex: 'order_status',
            key: 'order_status',
            align: 'center',
            render: text => {
                let color = '';
                if (text === 'Pending') color = 'orange'
                else if (text === 'Assigned') color = 'blue'
                else if (text === 'In Progress') color = 'purple'
                else if (text === 'Completed') color = 'cyan'
                return <span>
                    <Tag color={color} >
                        {text}
                    </Tag>
                </span>
            },
        },
        {
            title: 'Action',
            key: 'action',
            align: 'center',
            render: (text, record) => (
                <div>
                    {
                        record.order_status === "Assigned" &&
                        <Tooltip title="Pick Order">
                            <Button
                                type="primary"
                                className="mr-2"
                                icon={<DownloadOutlined />}
                                size="small"
                                onClick={() => pickOrder(record.order_id)}
                            />
                        </Tooltip>
                    }
                    {
                        record.order_status === "In Progress" &&
                        <>
                            <Tooltip title="View Deal">
                                <Button
                                    type="primary"
                                    className="mr-2"
                                    icon={<EyeOutlined />}
                                    size="small"
                                    onClick={() => history.push(`/orders/${record.order_token}/${record.order_id}`)}
                                />
                            </Tooltip>
                            <Tooltip title="Unpick Deal">
                                <Button
                                    type="primary"
                                    danger
                                    className="mr-2"
                                    icon={<UploadOutlined />}
                                    size="small"
                                    onClick={() => unPickDeal(record.order_id)}
                                />
                            </Tooltip>
                        </>
                    }
                </div>
            )
        },
    ];

    const getForwardedDeals = async () => {
        try {
            let res = await axios.post('/workorderlist', { user_id, role_id });
            setForwardedDeals(res.data.data)
            setLoadingForwarded(false);
        } catch (error) {
            console.log(error);
            setLoadingForwarded(false);
            error.response ?
                message.error(error.response.data)
                :
                message.error(error.message)
        }
    }

    const getPickedDeals = async () => {
        try {
            let res = await axios.post('/workorderlist', { user_id, role_id, type: 'Pick' });
            setPickedDeals(res.data.data)
            setLoadingPicked(false);
        } catch (error) {
            console.log(error);
            setLoadingPicked(false);
            error.response ?
                message.error(error.response.data)
                :
                message.error(error.message)
        }
    }

    const pickOrder = async (order_id) => {
        try {
            await axios.post('/updateorderstatusworker', { user_id, order_id });
            getForwardedDeals();
            getPickedDeals();
        } catch (error) {
            console.log(error);
            error.response ?
                message.error(error.response.data)
                :
                message.error(error.message)
        }
    }

    const unPickDeal = async (order_id) => {
        try {
            let body = {
                user_id,
                role_id,
                order_id
            }

            let res = await axios.post('/unpickorder', body);
            getForwardedDeals();
            getPickedDeals();
            message.success(res.data.message);
        } catch (error) {
            console.log(error);
            error.response ?
                message.error(error.response.data)
                :
                message.error(error.message)
        }
    }

    const backToManager = async (order_token) => {
        try {
        } catch (error) {
            console.log(error);
            error.response ?
                message.error(error.response.data)
                :
                message.error(error.message)
        }
    }

    React.useEffect(() => {
        getForwardedDeals();
        getPickedDeals();
    }, []);

    return (
        <>
            <Card>
                <h3>Forwarded Orders</h3>
                <Row style={{ justifyContent: 'center' }}>
                    <Col lg={22}>
                        <Card bodyStyle={{ 'padding': '0px' }}>
                            <Table
                                columns={columns}
                                dataSource={forwardedDeals}
                                loading={loadingForwarded}
                                rowKey='order_id'
                            />
                        </Card>
                    </Col>
                </Row>
            </Card>
            <Card>
                <h3>Picked Orders</h3>
                <Row style={{ justifyContent: 'center' }}>
                    <Col lg={22}>
                        <Card bodyStyle={{ 'padding': '0px' }}>
                            <Table
                                columns={columns}
                                dataSource={pickedDeals}
                                loading={loadingPicked}
                                rowKey='order_id'
                            />
                        </Card>
                    </Col>
                </Row>
            </Card>
        </>
    )
}

const mapStateToProps = state => ({
    user_id: state.auth.user.user_id,
    role_id: state.auth.user.role_id
});

export default connect(mapStateToProps)(DesignerOrders);